import removeSlashFromBagination from '../../common/removeSlashpagination';
import thumparallax from '../../common/thumparallax';
export default {
  data() {
    return {
      swiperImageOptions: {
        speed: 800,
        effect: "fade",
        spaceBetween: 0,
        loop: true,
        parallax: true,
        slidesPerView: 1,
        navigation: {
          prevEl: ".swiper-button-prev",
          nextEl: ".swiper-button-next",
        },
        pagination: {
          type: "fraction",
          clickable: true,
          el: ".swiper-pagination",
        },
      },
      swiperTextOptions: {
        speed: 800,
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        navigation: {
          prevEl: ".controls .swiper-button-prev",
          nextEl: ".controls .swiper-button-next",
        },
        pagination: {
          type: "fraction",
          clickable: true,
          el: ".controls .swiper-pagination",
        },
      },
    };
  },
  computed: {
    swiper() {
      return this.$refs.myImageSwiper.$swiper;
    },
  },
  methods: {
    handleSwiperReadied: (swiper) => {
      setTimeout(() => {
        for (var i = 0; i < swiper.slides.length; i++) {
          let img = swiper.slides[i].childNodes[0].childNodes[0].childNodes[0]
          img.setAttribute(
            "data-swiper-parallax",
            0.75 * swiper.width
          );
        }
      });
    },
  },
  mounted() {
    removeSlashFromBagination()
    thumparallax()
  },
};
